.page__body {
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: transform .3s, opacity .3s;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-20px);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

@media (min-width: 1280px) {
  .page__menu {
    visibility: hidden;
    pointer-events: none;
    height: 0;
    padding-top: 0;
